"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { 
  Table, 
  TableBody, 
  TableCell, 
  TableHead, 
  TableHeader, 
  TableRow 
} from "@/components/ui/table";
import { 
  Download, 
  Trash2, 
  Eye, 
  Calendar, 
  DollarSign,
  Star,
  Loader2
} from "lucide-react";

interface SavedGig {
  id: string;
  title: string;
  niche: string;
  description: string;
  reviews: Array<{
    name: string;
    rating: number;
    feedback: string;
  }>;
  pricing: {
    basic: { price: number };
    standard: { price: number };
    premium: { price: number };
  };
  thumbnails: string[];
  generatedAt: string;
  savedAt: string;
}

export default function AdminDashboard() {
  const [savedGigs, setSavedGigs] = useState<SavedGig[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  useEffect(() => {
    fetchSavedGigs();
  }, []);

  const fetchSavedGigs = async () => {
    try {
      setIsLoading(true);
      const response = await fetch("/api/saveGig");
      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Failed to fetch saved gigs");
      }

      setSavedGigs(data.data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to load gigs");
    } finally {
      setIsLoading(false);
    }
  };

  const exportAllGigs = () => {
    try {
      const dataStr = JSON.stringify(savedGigs, null, 2);
      const dataBlob = new Blob([dataStr], { type: "application/json" });
      const url = URL.createObjectURL(dataBlob);
      
      const link = document.createElement("a");
      link.href = url;
      link.download = `fiverr-gigs-export-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      URL.revokeObjectURL(url);
      setSuccess("Gigs exported successfully!");
    } catch (err) {
      setError("Failed to export gigs");
    }
  };

  const exportAsCSV = () => {
    try {
      const headers = ["ID", "Title", "Niche", "Basic Price", "Standard Price", "Premium Price", "Avg Rating", "Generated At", "Saved At"];
      
      const csvData = savedGigs.map(gig => {
        const avgRating = gig.reviews.reduce((sum, review) => sum + review.rating, 0) / gig.reviews.length;
        return [
          gig.id,
          `"${gig.title}"`,
          `"${gig.niche}"`,
          gig.pricing.basic.price,
          gig.pricing.standard.price,
          gig.pricing.premium.price,
          avgRating.toFixed(1),
          new Date(gig.generatedAt).toLocaleDateString(),
          new Date(gig.savedAt).toLocaleDateString()
        ].join(",");
      });

      const csvContent = [headers.join(","), ...csvData].join("\n");
      const dataBlob = new Blob([csvContent], { type: "text/csv" });
      const url = URL.createObjectURL(dataBlob);
      
      const link = document.createElement("a");
      link.href = url;
      link.download = `fiverr-gigs-export-${new Date().toISOString().split('T')[0]}.csv`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      URL.revokeObjectURL(url);
      setSuccess("CSV exported successfully!");
    } catch (err) {
      setError("Failed to export CSV");
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit"
    });
  };

  const getAverageRating = (reviews: SavedGig['reviews']) => {
    if (reviews.length === 0) return 0;
    return reviews.reduce((sum, review) => sum + review.rating, 0) / reviews.length;
  };

  const truncateText = (text: string, maxLength: number) => {
    return text.length > maxLength ? text.substring(0, maxLength) + "..." : text;
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center space-y-4">
          <Loader2 className="w-8 h-8 animate-spin mx-auto" />
          <p className="text-gray-600">Loading saved gigs...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
          <p className="text-gray-600 mt-1">
            Manage and export your generated Fiverr gigs
          </p>
        </div>
        <div className="flex gap-2">
          <Button onClick={exportAsCSV} variant="outline">
            <Download className="w-4 h-4 mr-2" />
            Export CSV
          </Button>
          <Button onClick={exportAllGigs}>
            <Download className="w-4 h-4 mr-2" />
            Export JSON
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Gigs</p>
                <p className="text-2xl font-bold">{savedGigs.length}</p>
              </div>
              <Eye className="w-8 h-8 text-blue-500" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Avg Basic Price</p>
                <p className="text-2xl font-bold">
                  ${savedGigs.length > 0 
                    ? Math.round(savedGigs.reduce((sum, gig) => sum + gig.pricing.basic.price, 0) / savedGigs.length)
                    : 0}
                </p>
              </div>
              <DollarSign className="w-8 h-8 text-green-500" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Avg Rating</p>
                <p className="text-2xl font-bold">
                  {savedGigs.length > 0 
                    ? (savedGigs.reduce((sum, gig) => sum + getAverageRating(gig.reviews), 0) / savedGigs.length).toFixed(1)
                    : "0.0"}
                </p>
              </div>
              <Star className="w-8 h-8 text-yellow-500" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">This Month</p>
                <p className="text-2xl font-bold">
                  {savedGigs.filter(gig => {
                    const gigDate = new Date(gig.savedAt);
                    const now = new Date();
                    return gigDate.getMonth() === now.getMonth() && 
                           gigDate.getFullYear() === now.getFullYear();
                  }).length}
                </p>
              </div>
              <Calendar className="w-8 h-8 text-purple-500" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Status Messages */}
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {success && (
        <Alert>
          <AlertDescription className="text-green-700">{success}</AlertDescription>
        </Alert>
      )}

      {/* Gigs Table */}
      <Card>
        <CardHeader>
          <CardTitle>Saved Gigs</CardTitle>
        </CardHeader>
        <CardContent>
          {savedGigs.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-gray-500">No saved gigs found.</p>
              <p className="text-sm text-gray-400 mt-1">
                Generate some gigs first to see them here.
              </p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Title</TableHead>
                    <TableHead>Niche</TableHead>
                    <TableHead>Description</TableHead>
                    <TableHead>Pricing</TableHead>
                    <TableHead>Rating</TableHead>
                    <TableHead>Created</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {savedGigs.map((gig) => (
                    <TableRow key={gig.id}>
                      <TableCell className="font-medium">
                        {truncateText(gig.title, 30)}
                      </TableCell>
                      <TableCell>
                        <Badge variant="secondary">{gig.niche}</Badge>
                      </TableCell>
                      <TableCell className="max-w-xs">
                        {truncateText(gig.description, 60)}
                      </TableCell>
                      <TableCell>
                        <div className="text-sm">
                          <div>${gig.pricing.basic.price} - ${gig.pricing.premium.price}</div>
                        </div>
                      </TableCell>
                      <TableCell>
                        <div className="flex items-center gap-1">
                          <Star className="w-4 h-4 fill-yellow-400 text-yellow-400" />
                          <span>{getAverageRating(gig.reviews).toFixed(1)}</span>
                        </div>
                      </TableCell>
                      <TableCell className="text-sm text-gray-600">
                        {formatDate(gig.savedAt)}
                      </TableCell>
                      <TableCell>
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => {
                              const gigData = JSON.stringify(gig, null, 2);
                              navigator.clipboard.writeText(gigData);
                              setSuccess("Gig data copied to clipboard!");
                            }}
                          >
                            <Eye className="w-4 h-4" />
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
